
@use '../../styles/theme/variables' as *;

.game-text-container {
  font-size: 15px;
  line-height: 1.8;
  color: #000000; // As per user request
  white-space: pre-wrap;

  .text-dialogue {
    color: $color-gold-liu;
  }

  .text-psychology {
    color: $color-purple-mystery;
    font-style: italic;
  }

  .text-scenery {
    color: $color-cyan-tian;
  }

  .text-system-highlight {
    color: $color-gold-liu;
    font-weight: bold;
  }
}
